<template>
  <div class="add container">
  <router-link to="/repair log"></router-link>
  <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header">  </h1>
  <form v-on:submit="addReplog">
  <div class="well">
            <h4>   </h4>
            <div class="form-group">
                <label> </label>
                <input type="text" class="form-control" placeholder=" " v-model="replog.id_registry">
            </div>
            <div class="form-group">
                <label> </label>
                <input type="text" class="form-control" placeholder=" " v-model="replog.id_employee">
            </div>
            <div class="form-group">
                <label></label>
                <input type="text" class="form-control" placeholder="" v-model="replog.Date">
            </div>
             <div class="form-group">
                <label></label>
                <input type="text" class="form-control" placeholder="" v-model="replog.Time">
            </div>
        </div>
        <button type="" class="btn btn-primary"></button>
    </form>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        replog: {},
        alert:''
        }
    },
    methods: {
      addReplog(e){
          if(!this.replog.id_registry || !this.replog.id_employee || !this.replog.Date || !this.replog.Time){
                this.alert = ',    ';
            } else {
                let newReplog = {
                    id_registry: this.replog.id_registry,
                    id_employee: this.replog.id_employee,
                    Date: this.replog.Date,
                    Time: this.replog.Time
                }
                this.$http.post('http://g9061745.beget.tech/api/repair_log/add', newReplog)
                    .then(function(response){
                        this.$router.push({path: '/repair log', query: {alert: '  '}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
        }
    },
    components:{
        Alert
    }
}
</script>

