<template>
  <div class="edit container">
  <h1 class="page-header"> </h1>
  <form v-on:submit="updateTech">
  <div class="well">
            <h4>  </h4>
            <div class="form-group">
                <label> </label>
                <input type="text" class="form-control" placeholder=" " v-model="tech.name">
            </div>
            <div class="form-group">
                <label></label>
                <input type="text" class="form-control" placeholder="" v-model="tech.producer">
            </div>
            <div class="form-group">
                <label> </label>
                <input type="text" class="form-control" placeholder=" " v-model="tech.serial_number">
            </div>
            <div class="form-group">
                <label> </label>
                <input type="text" class="form-control" placeholder=" " v-model="tech.Year_of_manufacture">
            </div>
        </div>
        <button type="" class="btn btn-primary"></button>
    </form>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        tech: {},
        alert:''
        }
    },
    methods: {
        fetchTech(id){
            this.$http.get('http://g9061745.beget.tech/api/technique/'+id)
            .then(function(response){
                this.tech = response.body;
            });
        },
      updateTech(e){
          if(!this.tech.name || !this.tech.producer || !this.tech.serial_number || !this.tech.Year_of_manufacture){
                this.alert = ',    ';
            } else {
                let updTech = {
                    name: this.tech.name,
                    producer: this.tech.producer,
                    serial_number: this.tech.serial_number,
                    Year_of_manufacture: this.tech.Year_of_manufacture
                }
                this.$http.put('http://g9061745.beget.tech/api/technique/update/'+this.$route.params.id, updTech)
                    .then(function(response){
                        this.$router.push({path: '/technique', query: {alert: ' '}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
    }
},
created: function(){
        this.fetchTech(this.$route.params.id);
    },
    components:{
        Alert
    }
    }
</script>

